body {
  background-color: #1a1a1a;
  color: #e6e6e6;
  font-size: 1em;
}

#main {
  width: fit-content;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 24px 0;
  box-sizing: border-box;
}

#title {
  margin: 0;
}

.table_container {
  position: relative;
  border-radius: 12px;
  overflow: auto;
  width: fit-content;
  border: 2px solid #404040;
}

thead {
  position: sticky;
  top: 0;
  left: 0;
  background-color: #262626;
}

.table_header th {
  border-bottom: 2px solid #404040;
}

table {
  border-spacing: 0px;
  background-color: #262626;
}

th {
  cursor: pointer;
  user-select: none;
}

tr:not(:last-child) td,
tr:not(:last-child) th {
  border-bottom: 2px solid #404040;
}

td,
th {
  text-align: left;
  margin: 0;
  padding: 8px 12px;
}

td:not(:last-child),
th:not(:last-child) {
  border-right: 2px solid #404040;
}

tr:nth-child(even) {
  background-color: #333333;
}

.selected {
  background-color: #666666;
}

.primary {
  background-color: #333333;
}

.selected.primary {
  background-color: #999999;
}
